'use strict';

const anchorLinks = document.querySelectorAll('a');
const primaryImgDiv = document.querySelector('#largeImg').parentNode;

anchorLinks.forEach((anchor) => anchor.addEventListener('click', (evt) => {
  evt.preventDefault();

  primaryImgDiv.innerHTML = `
    <img id="largeImg" src="${anchor.href}"
    alt="main image" class="gallery__large-img gallery__img">
  `;
}));
